import { Component, OnInit, Input, Output } from '@angular/core';
import {Router} from '@angular/router';
import {AppComponent} from '../app.component';
import {User, WelcomeService} from '../welcome.service';
import {HttpClientModule} from '@angular/common/http'

@Component({
  selector: 'app-welcome',
  templateUrl: './welcome.component.html',
  styleUrls: ['./welcome.component.css']
})

export class WelcomeComponent implements OnInit {
  
  email: string;
  password: string;
  id2: number;
  logged: string[];
  user: User[]
  public loggedIn: boolean = false;
  selectedFile: File = null;
  blob: Blob;

  onFileSelected(event){
    console.log(event);
    this.selectedFile = <File>event.target.files[0];
    this.blob = this.selectedFile as Blob;
    console.log("blob" + this.blob)
  }

  onUpload(){
    //this.default_img(localStorage.getItem("ID"), this.blob);
  }

  constructor(
    public userService: WelcomeService,
    private router: Router,
    private http: HttpClientModule
  ) { }


  ngOnInit() {
    if(localStorage.getItem("loggedIn") == "true"){
      this.loggedIn = true;
      this.get_userdata(localStorage.getItem("ID"));
    }
    else{
      this.loggedIn = false;
      localStorage.clear();
      localStorage.setItem("loggedIn", "false");
    }
  }

  get_userdata(ID: any){
    this.userService.get_userdata(ID).subscribe(
      data => {
        this.user= data;
      },
      error => {
        alert("unable to get user data");
      }
    )
  }

  get_login(email: string, password: string) {
    this.userService.get_login(email, password).subscribe(
      data => {
        this.user = data;
        localStorage.setItem("loggedIn","true");
        localStorage.setItem("ID", this.user["ID"]);
        console.log("signed in");
        this.loggedIn = true;
      },
      error => {
        alert('incorrect username or password');
      }
    );
  }

  logout(){
    localStorage.clear();
    localStorage.setItem("loggedIn", "false");
    this.loggedIn = false;
  }

  update_img(id: any, blob: Blob){
    this.userService.update_img(id, blob).subscribe(
      data => {
        console.log("inserted image.");
      },
      error => {
        alert('error!!!');
      }
    )
  }

  default_img(id:any, blob:Blob){
    this.userService.default_img(id, blob).subscribe(
      data => {
        console.log("uploaded default img");
      }
    )
  }
}
